<template>
  <v-app>
    <v-app-bar class="d-print-none" app elevation="3" color="white">
      <v-btn icon @click="$router.go(-1)">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <v-toolbar-title>Export Halaman Ke Pdf</v-toolbar-title>
      <v-spacer></v-spacer>
      <ExportPdf v-model="namaPendamping"></ExportPdf>
    </v-app-bar>
    <v-content class="grey lighten-3 pt-0">
      <section class="d-flex justify-center">
        <div class="a4 pa-12">
          <h1 class="text-center display-1 font-weight-bold">LOG PENDAMPINGAN</h1>
          <table class="mt-3">
            <tr v-for="item in desc" :key="item.key">
              <td>{{ item.key }}</td>
              <td><span class="ml-12">:</span></td>
              <td>{{ item.value }}</td>
            </tr>
          </table>
          <div class="box mt-12">
            <div class="box-title" style="background-color: rgb(231, 194, 27);">
              <b>REVIEW SPRINT SEBELUMNYA</b>
            </div>
            <div class="box-body">
              ss
            </div>
          </div>
          <div class="box mt-12">
            <div class="box-title" style="background-color: rgb(231, 194, 27);">
              <b>REVIEW SPRINT SEKARANG</b>
            </div>
            <div class="box-body">
              ss
            </div>
          </div>
          <div class="mt-12">
            <div>Yogyakarta, {{today}}</div>
            <div class="d-flex justify-space-between">
              <div class="text-center">
                <p>Tenant</p>
                <div class="mt-12">Fandu</div>
                <div>CEO Smartedu</div>
              </div>
              <div class="text-center">
                <p>Pendamping</p>
                <div v-if="namaPendamping === ''" class="mt-12 red--text caption">
                  (Nama pendamping dimasukan sebelum diexport)
                </div>
                <div v-else class="mt-12">{{ namaPendamping }}</div>
                <div>Pendamping</div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </v-content>
  </v-app>
</template>

<script>
import { mapState } from "vuex";
import ExportPdf from './ExportPdf'
export default {
  props: ['sprint_id'],
  data: () => ({
    desc: [
      { key: 'NAMA TENANT', value: 'Smartedu' },
      { key: 'CEO', value: 'Fandu' },
      { key: 'SPRINT', value: 'minggu 3' },
      { key: 'TANGGAL', value: '24 September 2020' }
    ],
    namaPendamping: ''
  }),
  components: { ExportPdf },
  created() {
    this.dataSprintStartup()
  },
  computed: {
    today() {
      const bulan = [
        'Januari', 'Februari',
        'Maret', 'April', 'Mei',
        'Juni', 'Juli', 'Agustus',
        'September', 'Oktober',
        'November', 'Desember'
      ]
      const date = new Date()
      const day = date.getDate()
      const month = date.getMonth()
      const year = date.getFullYear()

      return `${day} ${bulan[month]} ${year}`
    },
    ...mapState('dataStartupSprint', ['dataSprint'])
  },
  methods: {
    dataSprintStartup() {
			this.$store.dispatch('dataStartupStartup/getDataStartup')
			this.$store.dispatch('dataStartupSprint/getDataSprint')
      this.$store.dispatch('dataStartupSprint/getDataList', this.sprint_id)
    }
  }
}
</script>

<style scoped>
  .a4{
    width: 21cm;
    min-height: 29.7cm;
    justify-self: center;
    background-color: #ffffff;
    color: #000 !important; 
    margin-top: 100px;
    margin-bottom: 64px;
  }
  .box{
    border: 1px solid #000;
  }
  .box-title{    
    text-align: center;
    border-bottom: 1px solid #000;
  }
  .box-body{
    padding: 20px;
  }
  @media print{
    .d-print-none{ display: none; }
    .a4{ margin-top: 0px;margin-bottom: 0px; }
    .page-break{
      display: block;
      page-break-after: auto;
    }
  }
</style>