<template>
	<v-dialog
    class="d-print-none"
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
			<v-btn
        depressed
        color="cyan"
        v-on="on"
        id="my-canvas"
        dark>
        <v-icon class="mr-2">mdi-export</v-icon>
        export ke pdf
      </v-btn>
    </template>

    <v-card>
      <div class="pa-2 text-center">
        <span class="title">Masukan nama pendamping</span>
      </div>
      <v-form v-model="valid" ref="form">
        <v-card-text>
          <v-text-field
            v-model="nama"
            label="Nama"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field> 
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            export
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<style scoped>
  @media print{
    .d-print-none{
      display: none;
    }
  }
</style>

<script>
  import { mapState } from 'vuex'

  export default {
    model: {
      props: 'nama',
      event: 'input'
    },
    data () {
      return {
        dialog: false,
        nama: '',
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],
      }
    },

    computed: {
      ...mapState('dataStartupSprint', ['isLoading', 'status']),      
    },

    watch: {
      nama(val) {
        this.$emit('input', val)
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.dialog = false
          setTimeout(() => window.print(), 1000)
        }
      }
    }
  }
</script>